<template>
  <div class="DeleteField">
    <el-input placeholder=""  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
<el-table-column
    prop="fieldid"
    label="ID"
    width="150%"
    fixed>
    </el-table-column>
    <el-table-column
      prop="fieldname"
      label=""
      width="150%">
    <template slot-scope="scope">
      <span v-html="format(scope.row.fieldname)"></span>
    </template>
    </el-table-column>
    <el-table-column
      prop="fieldtype"
      label=""
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldtype)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldstate"
      label=""
      width="200%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.fieldstate)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="fieldwidth"
      label=""
      width="200%">
    </el-table-column>
    <el-table-column
      prop="fieldlong"
      label=""
      width="200%">
    </el-table-column>
        <el-table-column
      prop="unitprice"
      label="()"
      width="200%">
    </el-table-column>
    <el-table-column
      prop="address"
      label=""
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.address)"></span>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label=""
      style="text-align: center"
      width="100%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="changeRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  
  <el-dialog title="" :visible.sync="dialogFormVisible" fullscreen :close-on-press-escape=false :show-close=false>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
	<el-form-item label="ID" prop="fieldid">
		<el-input v-model="ruleForm.fieldid" disabled style="width: 75%"></el-input>
	</el-form-item>
  <el-form-item label="" prop="fieldname">
    <el-input v-model="ruleForm.fieldname" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="" prop="fieldinout"> 
    <el-select v-model="ruleForm.fieldinout" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" status-icon prop="fieldtype">
    <el-select v-model="ruleForm.fieldtype" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select> 
  </el-form-item>
  <el-form-item label="" prop="fieldimg" status-icon>
  <el-form-item label="" prop="fieldimg" status-icon>
  	<el-select v-model="ruleForm.fieldimg" placeholder="">
		<el-option label="" value="static/img/iBadminton.jpg"></el-option>
		<el-option label="" value="static/img/oBadminton.jpg"></el-option>
		<el-option label="" value="static/img/1.jpg"></el-option>
		<el-option label="" value="static/img/2.jpg"></el-option>
		<el-option label="" value="static/img/iBasketball.jpg"></el-option>
		<el-option label="" value="static/img/oBasketball.jpg"></el-option>
		<el-option label="" value="static/img/iTableTennis.jpg"></el-option>
		<el-option label="" value="static/img/billiards.jpg"></el-option>
		<el-option label="" value="static/img/oTennis.jpg"></el-option>
		<el-option label="" value="static/img/iVolleyball.jpg"></el-option>
		<el-option label="" value="static/img/oVolleyball.jpg"></el-option>
		<el-option label="" value="static/img/oFootball.jpg"></el-option>
	</el-select>
  </el-form-item>
	<div>
		<el-image :src="ruleForm.fieldimg" style="margin-top:10px;width: 400px;height: 200px" lazy>
	      <div slot="error" class="image-slot">
	        <i class="el-icon-picture-outline"></i>
	      </div>
	    </el-image>
	</div>
  </el-form-item>
  <el-form-item label="" required>
    <el-col :span="8">
    <el-form-item prop="fieldlong">
      <span style="margin-left: 1%;margin-right: 1%">()&nbsp;:&nbsp;</span>
      <el-input v-model.number="ruleForm.fieldlong" placeholder="" ></el-input>
    </el-form-item>
    </el-col>
    <el-col :span="2">&nbsp;</el-col>
    <el-col :span="8">  
    <el-form-item prop="fieldwidth">
    <span style="margin-left: 1%;margin-right: 1%">()&nbsp;:&nbsp;</span>
    <el-input v-model.number="ruleForm.fieldwidth" placeholder="" ></el-input>
  </el-form-item>
  </el-col>
  </el-form-item>
  <el-form-item label="" prop="fieldstate">
    <el-radio-group v-model="ruleForm.fieldstate">
      <el-radio label="" value=""></el-radio>
      <el-radio label="" value=""></el-radio>
      <el-radio label="" value=""></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="30()" prop="unitprice">
    <el-input type="text" v-model.number="ruleForm.unitprice" style="width: 72%"></el-input>
  </el-form-item>
  <el-form-item label="" prop="address">
    <el-input type="textarea" v-model="ruleForm.address" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    <el-button type="info" @click="cancelForm('ruleForm')"></el-button>
  </el-form-item>
</el-form>
  </el-dialog>
  </div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'

export default {
  data () {
    var checkLW = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error(''));
          } else {
              callback();
          }
        }, 1000);
      };
    return {
      search: '',
      TempValue:{
        fieldid:'',
        fieldimg:'',	
        fieldtype:'',
        fieldinout:'',
        fieldstate:'',
        fieldlong:'',
        fieldwidth:'',
        fieldname:'',
        address:'',
        unitprice:''
      },
      ruleForm: {
    		fieldid:'',
    		fieldimg:'',	
    		fieldtype:'',
    		fieldinout:'',
    		fieldstate:'',
    		fieldlong:'',
    		fieldwidth:'',
    		fieldname:'',
    		address:'',
    		unitprice:''
        },
      tableDataL:6,
      dialogFormVisible:false,
      tableData: [
        
        {
			fieldid:'',
			fieldimg:'',	
			fieldtype:'',
			fieldinout:'',
			fieldstate:'',
			fieldlong:'',
			fieldwidth:'',
			fieldname:'',
			address:'',
			unitprice:''
		}
      ],
      rules: {
          fieldname: [
            { required: true, message: '', trigger: 'blur' },
            { min: 0, max: 10, message: ' 0  10 ', trigger: 'blur' }
          ],
          fieldinout: [
            { required: true, message: '', trigger: 'change' }
          ],
          fieldtype:[
            {required:true,message:'',trigger:'change'}
          ],
		  fieldlong:[
        	{validator:checkLW,trigger:'blur'}
      ],
      	  fieldwidth:[
        	{validator:checkLW,trigger:'blur'}
      ],
          fieldstate: [
            { required: true, message: '', trigger: 'change' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' }
          ],
          unitprice:[
          	{validator:checkLW,trigger:'blur'}
          ]
        }
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    }
  },

  methods: {

     changeRow(scope, rows,searchTable) {
      var temp={
        fieldid:'',
		fieldimg:'',	
		fieldtype:'',
		fieldinout:'',
		fieldstate:'',
		fieldlong:'',
		fieldwidth:'',
		fieldname:'',
		address:''
      };
      if (scope.row.fieldid!='') {
       //console.log('$index:'+scope.$index+'     row.tIndex:+scope.row.tIndex'+'     Datalength:'+rows.length+'     Searchlength:'+searchTable.length+"               this.search !== '':"+(this.search !== ''));
       this.dialogFormVisible=true;
       temp=JSON.parse(JSON.stringify(scope.row));
       this.TempValue=temp;
       this.ruleForm=scope.row;
       // 
      	
      }else{
      	this.$message.error('');
      }
       

       
      },
		allSite(){
			axios({
				url:'http://localhost:8080/GMS/field/getAllField.do',
				method:'post',
				headers:{
					'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
					'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
				},
			}).then(resp => {
				this.tableData = resp.data;
			}).catch(error =>{
				console.log('err');
			})
		},
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
    submitForm(formName) {
        //console.log(JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm));
        this.$refs[formName].validate((valid) => {
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
            //!!
          if (valid&&JSON.stringify(this.TempValue)!=JSON.stringify(this.ruleForm)) {
             var control = false;
             axios({
             	url:'http://localhost:8080/GMS/field/changeField.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
  					},
					data:qs.stringify(this.ruleForm)
             }).then(resp => {
             	var tempC = resp.data;
             	if (tempC==='Success to ChangeField') {
						control=true;
					}
             	if (control) {
					setTimeout(() => {
						loading.close();
						this.$notify({
						title: '',
						message: '',
						position:'bottom-right',
						type: 'success'
					});
						this.dialogFormVisible=false;
						return true;
					},1000);
					control=false;
             	}
             }).catch(err => {
             	console.log('err');
             })
        
          } else if (valid&&JSON.stringify(this.TempValue)==JSON.stringify(this.ruleForm)) {
				setTimeout(() => {
					loading.close();
					this.$notify({
						title: '',
						message: '',
						position:'bottom-right',
						type: 'warning'
					});
						return false;
					},300);
          }else {
            console.log('error submit!!');
            
            setTimeout(() => {
              loading.close();
              this.$notify({
              title: '',
              message: '',
              position:'bottom-right',
              type: 'error'
            });
            return false;
            },300);
            
          }
        });
      },
      cancelForm(formName){
        //
        this.ruleForm=this.TempValue;/*JSON.parse(JSON.stringify(this.TempValue));*/
          this.$refs.ruleForm.resetFields();
          this.dialogFormVisible=false;
        
        
      }
  },
  created(){
  	this.allSite();
  }
}

</script>

<style scoped>

</style>



