<template>
	<div id="msgItemOrder">
		<el-table
    	:data="formData"
		border
		>
    <el-table-column type="expand"
    label="">
      <template slot-scope="props">
        <el-form 
        label-position="left" 
        inline 
        class="demo-table-expand"
        >
          <el-form-item label="">
            <span>{{props.row.orderid}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{props.row.rentstart}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{props.row.rentend}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{props.row.orderstate}}</span>
          </el-form-item>
          <el-form-item label="ID">
          	<span>{{props.row.fieldid}}</span>
          </el-form-item>
          <el-form-item label="">
          	<span>{{props.row.fieldname}}</span>
          </el-form-item>
          <el-form-item label="">
          	<span>{{props.row.phone}}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
		label="ID"
		prop="orderid">
	</el-table-column>
	<el-table-column
		label=""
		prop="rentstart"
		>		
	</el-table-column>
	<el-table-column
		label=""
		prop="rentend"
		>
	</el-table-column>
	<el-table-column
	label=""
	prop="orderstate">
	</el-table-column>
	<el-table-column
      fixed="right"
      label=""
      width="150px">
      <template slot-scope="scope">
      	<el-button 
      		:disable="IFOrder"
      		type="primary"
      		@click.native.prevent="pay(scope)"
      		size="mini">
      		
      	</el-button>
        <el-button
          id="myCancelBtn"
          @click.native.prevent="CancelOrder(scope, formData)"
          type="danger"
          size="mini">
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>


<el-dialog title="" :visible.sync="dialogFormVisible" style="width:110%;">
  <el-form :model="ruleForm">
    <el-form-item label="" label-width="100px" style="margin-left:-30px">
      <el-input v-model="ruleForm.money" disabled></el-input>
    </el-form-item>
    <el-form-item label="()" label-width="100px" style="margin-left:-30px">
    	<el-input v-model="ruleForm.allTime" disabled></el-input>
    </el-form-item>
    <el-form-item label="ID" label-width="100px" style="margin-left:-30px">
    	<el-input v-model="ruleForm.fieldid" disabled></el-input>
    </el-form-item>
    <el-form-item label="" label-width="100px" style="margin-left:-30px">
    	<el-input v-model="ruleForm.fieldname" disabled></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false" style="width:20%"> </el-button>
    <el-button type="primary" @click="submitForm('ruleForm')" style="width:20%"> </el-button>
  </div>
</el-dialog>
	</div>
	
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import moment from 'moment'
	export default {
		inject:["reload"],
		data(){
			return {
				IFOrder:true,
				dialogFormVisible :false,
				userid:'',
				formData:[
					{
						orderid:'',
						rentstart:'',
						rentend:'',
						orderstate:'',
						fieldid:'',
						fieldname:'',
						phone:'',
						allTime:'',
						unitprice:'',
						money:'',
						payid:''
					},
				],
				ruleForm:{
					money:'',
					payid:'',
					allTime:'',
					fieldid:'',
					fieldname:''
				}
			}
		},
		methods:{
			CancelOrder(scope, rows) {
				var control = false;
				const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
		        if(scope.row.orderid!=''){
		        	var tempdata={
		        		orderid:'',
		        		fieldid:''
		        	}
		        	tempdata.orderid = scope.row.orderid;
		        	tempdata.fieldid = scope.row.fieldid;
		        	axios({
		        		url:'http://localhost:8080/GMS/FieldOrder/delOrder.do',
		        		method:'post',
						headers:{
			  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
	  					},
	  					data:qs.stringify(tempdata)
		        	}).then(resp => {

		        		var tempC = resp.data;
		        		if (tempC=='Success to CancelOrder') {
							control=true;
						}
						if(control){
							setTimeout(() => {
								loading.close();
						        this.$notify({
						          title: '',
						          message: '',
						          position:'bottom-right',
						          type: 'warning'
						        });
						        this.reload();
							},1000);	
						}else{
							setTimeout(() => {
								loading.close();
							    this.$notify({
							      title: '',
							      message: '',
							      position:'bottom-right',
							      type: 'error'
							    });
							},500);
						}

		        	})

		        }else{
			        this.$notify({
			          title: '',
			          message: '',
			          position:'bottom-right',
			          type: 'error'
			        });
					setTimeout(() => {
						loading.close();
						// this.formData.
					},300);
		        }
      		},
      		pay(scope){
      			//
      			//
      			this.dialogFormVisible=true;
      			this.ruleForm.money=scope.row.money;
      			this.ruleForm.allTime=scope.row.allTime;
      			this.ruleForm.fieldid=scope.row.fieldid;
      			this.ruleForm.fieldname=scope.row.fieldname;
      			this.ruleForm.payid=scope.row.payid;

      			//console.log('paying'+scope.row.payid);

      		},
      		submitForm(formName){
      			//console.log(formName)
      		//	/updateStatePay.do
      		//	
      		var control = false;

      		const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
      		if(this.ruleForm.payid!=''){
	      		axios({
	             	url:'http://localhost:8080/GMS/pay/updateStatePay.do',
						method:'post',
						headers:{
			  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
	  					},
						data:qs.stringify(this.ruleForm)
	             }).then(resp => {
	             	var tempC = resp.data;
	             	//console.log(tempC);
	             	if (tempC=='Success to UpdatePayState') {
							control=true;
						}
	             	if (control) {
						setTimeout(() => {
							loading.close();
							this.$notify({
							title: '',
							message: '',
							position:'bottom-right',
							type: 'success'
						});
							this.dialogFormVisible=false;
						},1000);
						control=false;
						//
						this.reload();
	             	}
	             }).catch(err => {
	             	console.log('err');
	             })
      		}else{
      			setTimeout(() => {
      				loading.close();
					this.$notify({
					title: '',
					message: '',
					position:'bottom-right',
					type: 'error'
				});
					this.dialogFormVisible=false;
				},1000);
      		}

      		},
      		orderSelect(){
      			//userid
      			var usrid='';
      			usrid = this.userid;
      			var MorderLength = 0;
      			axios({
      				url:'http://localhost:8080/GMS/FieldOrder/getListEAll.do',
      				method:'post',
      				headers:{
						'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
					},
					data:qs.stringify({'userid':usrid})
      			}).then(resp => {
					if(resp.data!=''){
						var respp = resp.data;
						this.formData = resp.data;
						//console.log(parseInt(respp[0].rentstart));
						for(let i=0;i<resp.data.length;i++){
							//30
							this.formData[i].allTime = (parseInt(respp[i].rentend)-parseInt(respp[i].rentstart))/1000/60;
							//
							this.formData[i].money = (this.formData[i].allTime)/30*(respp[i].unitprice);


							//
							this.formData[i].rentstart = moment(parseInt(respp[i].rentstart)).format('YYYY-MM-DD hh:mm:ss');
							this.formData[i].rentend = moment(parseInt(respp[i].rentend)).format('YYYY-MM-DD hh:mm:ss');
						}
						
						//console.log(moment(parseInt(respp[0].rentstart)).format('YYYY-MM-DD hh:mm:ss'));
						//console.log(resp.data);
						;
						// $emit
						MorderLength = resp.data.length;
						this.$emit('MorderLength',MorderLength);
					}else{
							this.$message({
							type: 'warning',
							message: ''
							});  
						}
					//
					//this.numCo = this.NoticeList.length-1;
				}).catch(err => {
					console.log('err');
				})

      		},
      		getUidFormCookie(){
				let UsrMsg = this.$getCookie('UName');
				var uID = '';
				if (UsrMsg!='') {
					uID = UsrMsg[0];
					this.userid = parseInt(uID);
				}
				
      		}
		},
		created(){
			this.getUidFormCookie();
			this.orderSelect();
		}
	}
</script>

<style scoped>
	.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  /deep/ .el-button{
  	width: 40px;
  	font-size: 10px;
  	text-align: center;
  	padding-left: 10px;
  }

  /deep/ #myCancelBtn{
  	width: 70px;
  }
</style>


