<template>
  <el-carousel :interval="4000" type="card" :height="ImgH+'px'">
    <el-carousel-item v-for="item in imgList" :key="item.id">
      <div class="medium">
        <img :src="item.idView" ref="ImgH" alt="" style="width: 100%" @load="imgLoader" > 
      </div>
    </el-carousel-item>
  </el-carousel>
</template>

<script>
      export default {
        data(){
          return {
            //
            imgList:[
              {id:0,name:'',idView:require('../../../../static/img/1.jpg')},
              {id:1,name:'',idView:require('../../../../static/img/2.jpg')},
              {id:2,name:'',idView:require('../../../../static/img/billiards.jpg')},
              {id:3,name:'',idView:require('../../../../static/img/iBadminton.jpg')},
              {id:4,name:'',idView:require('../../../../static/img/oTennis.jpg')},
              {id:5,name:'',idView:require('../../../../static/img/oFootball.jpg')},
            ],
            ImgH:''         
          }
        },
        methods:{
          imgLoader(){
            this.$nextTick(() => {
              this.ImgH=this.$refs.ImgH[1].height;
              //console.log(this.$refs.ImgH[1].height);
            })
          }
        },
        // ,
        mounted(){
          this.imgLoader();
          // (resize)
          // this.$ref.name DOM
          // 
          // refmounted
          // mounted()
          window.addEventListener('resize',() => {
            this.ImgH = this.$refs.ImgH[1].height;
            this.imgLoader();
          },false)
        }
      }
</script>

<style scoped>
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>


