<template>
	<div class="available">
		<el-row :gutter="10">
		  <el-col 
		  :sm="12" 
		  :md="8" 
		  :lg="6" 
		  :xl="4" 
		  v-for="(site,index) in sites.slice((currentPage-1)*(nowPageNum),currentPage*nowPageNum)"
		  :key="index"
		  >
		  	<div class="grid-content bg-purple">
		  		<el-image :src="site.fieldimg" fit="fill" lazy style="width: 200px;height: 200px">
		  			<div slot="error" class="image-slot">
        				<i class="el-icon-picture-outline"></i>
      				</div>
		  		</el-image>
		  		<div style="width: 27%;height:200px; float: right;">
		  			<div style="height: 120px">
		  				<p>
	           			 	<h2>:</h2>
								<br>
	           			 	{{site.address}}...
			  			</p>
		  			</div>
		  			
            		<el-button type="text" @click="showField(index,site)" style="margin-left: 30px;margin-bottom: -10px"><i class="el-icon-view el-icon--right"></i></el-button>
            		<el-button type="primary" style="width:80%" @click="showOrder(index,site)"><i class="el-icon-s-order el-icon--right"></i></el-button>			            	
            	<!-- @click="Fmsg(index)" -->
            </div>
		  		<!-- <img :src="site.InsUrl" alt="" style="width: 200px;height: 200px"> -->
		  	</div>
		 </el-col>
		</el-row>

		<el-dialog lock-scroll title="" :visible.sync=dialogFormVisible :close-on-press-escape=false :show-close=false>
			<div style="height:550px">
        <el-card shadow="hover" style="height:350px;margin-top:-30px"body-style="{ padding: '0px' }">
				<el-image :src="tableSite.fieldimg" fit="fill" lazy style="width: 100%;height: 20%">
		  			<div slot="error" class="image-slot">
        				<i class="el-icon-picture-outline"></i>
      				</div>
		  		</el-image>
        </el-card>
		  		<div style="margin-top:20px;">
          <el-card shadow="hover" style="padding-left:20%;margin-top: 3px;height:200px"body-style="{ padding: '0px' }">
					<span style="font-size:16px">:{{tableSite.fieldname}}</span><br>
   			 	<span style="font-size:16px">{{tableSite.fieldtype}}</span><br>
   			 	<span style="font-size:16px">{{tableSite.fieldstate}}</span><br>
					<span style="font-size:16px">&nbsp;&nbsp;</span><br>
					<span style="font-size:16px">{{tableSite.fieldwidth}}&nbsp;&nbsp;</span><br>
					<span style="font-size:16px">{{tableSite.address}}</span><br>
					<span style="font-size:16px">(30){{tableSite.unitprice}}</span><br>
          </el-card>  
				</div>
		</div>
		</el-dialog>

		<el-dialog title="" lock-scroll :visible.sync=orderFormVisible fullscreen :close-on-press-escape=false :show-close=false>
			<el-form 
				:model="form" 
				status-icon 
				:rules="rules" 
				ref="form" 
				label-width="100px" 
				style="margin-left:25%"
				>
				<el-form-item label="ID" label-width="100px" prop="fieldid">
					<el-input v-model="form.fieldid" disabled style="width:58%"></el-input> 
				</el-form-item>
				<el-form-item label="" label-width="100px" prop="fieldname">
					<el-input v-model="fieldname" disabled style="width:58%"></el-input> 
				</el-form-item>
				<el-form-item label="userid" label-width="100px" prop="userid">
					<!--autocomplete="off"-->
					<el-input v-model="form.userid" disabled style="width:58%"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="100px" prop="name">
					<el-input v-model="name" disabled style="width:58%"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="100px" prop="phone">
					<el-input v-model="form.phone" autocomplete="off" style="width:58%"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="100px" prop="unitprice">
					<el-input v-model="form.unitprice" disabled style="width:56%"></el-input>
				</el-form-item>
					
				<el-form-item label="" label-width="100px">
					<div class="block">
						<el-date-picker
						v-model="form.startDate"
						type="date"
						format="yyyy-MM-dd"
						:default-value="form.startDate"
						value-format="timestamp"
						placeholder=""
						:picker-options="pickerOptions"
						style="width:150px">
						</el-date-picker>
						<el-time-select
						v-model="form.startTime"
						type="date"
						format="HH:mm"
						value-format="HH:mm"
						placeholder=""
						:picker-options="{
							start: '08:00',
							step: '00:15',
							end: '18:30',
							maxTime:'18:00',
							//
							minTime:(form.startDate==suretime)?new Date().getHours()+':'+new Date().getMinutes():'07:45'
						}"
						style="width:150px">
						</el-time-select>
					</div>
					<span id="notification">*</span>
				</el-form-item>
				<el-form-item label="" label-width="100px">
					<div class="block1">
						<el-date-picker
						v-model="form.endDate"
						type="date"
						format="yyyy-MM-dd"
						value-format="timestamp"
						placeholder=""
						:picker-options="pickerOptions"
						style="width:150px">
						</el-date-picker>
						<el-time-select
						v-model="form.endTime"
						type="date"
						format="HH:mm"
						value-format="HH:mm"
						placeholder=""
						:picker-options="{
							start: '08:30',
							step: '00:15',
							end: '18:30',
							minTime:(form.endDate!=='')&&(form.startTime!='')&&(form.endDate==form.startDate)?form.startTime:'8:15'
						}"

						style="width:150px">
						</el-time-select>
					</div>
				</el-form-item>
				<el-form-item style="margin-left:25%">
					<el-button type="primary" @click="submitForm('form')"></el-button>
					<el-button type="info" @click="cancelForm('form')"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>

		<div class="block" style="text-align: center;margin-top: 20px">
		    <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="pageSizes"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="totalnum">
		    </el-pagination>
 		</div>	
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import moment from 'moment'
	export default {
		inject:["reload"],
			data(){
				//var checkLW = (rule, value, callback) => {
			        //if (!value) {
			         // return callback(new Error(''));
			        //}
			       // setTimeout(() => {
			         // if (!Number.isInteger(value)) {
			          //  callback(new Error(''));
			         // } else {
			          //    callback();
			         // }
		        //}, 1000);

		        var checkLW = (rule, value, callback) => {
		        	if (value==='') {
		        		return callback(new Error(''));
		        	}
		        		setTimeout(() => {
		        			 if (value.length!==11) {
		        			 	callback(new Error('11'))
		        			 }else if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(value)) {
		        				callback(new Error('!'));
		        			 }else{
		        			 	callback();
		        			 }
		        		},1000);
		        	}
			return {
				pickerOptions:{
					disabledDate(time) {
						if(time.getTime() < Date.now()||time.getTime() > Date.now()+ 3600 * 1000 * 24 * 14){
							return time.getTime()-(time.getSeconds()*1000+time.getMilliseconds());
						}
			            
			          },
			          shortcuts:[{
			            text: '',
			            onClick(picker) {
			            	var time = new Date();
			            	var misecond = time.getHours()*60*60*1000+time.getMinutes()*60*1000+time.getSeconds()*1000+time.getMilliseconds();
			            	//console.log(time.getTime()-misecond);
			              picker.$emit('pick', time.getTime()-misecond);
			            }
			          },{
			            text: '',
			            onClick(picker) {
			              const time = new Date();
			              var misecond = time.getHours()*60*60*1000+time.getMinutes()*60*1000+time.getSeconds()*1000+time.getMilliseconds();
			              time.setTime(time.getTime() + 3600 * 1000 * 24 * 7-misecond);
			              picker.$emit('pick', time);
			            }
			          }]
				},
				fieldname:'',
				name:'',
				suretime:'',
				orderFormVisible:false,
				dialogFormVisible:false,
				// InsUrl:require('../../../../../assets/2.jpg')
					tableSite:{
							fieldid:'',
							fieldimg:'',
							fieldtype:'',
							fieldinout:'',
							fieldstate:'',
							fieldlong:'',
							fieldwidth:'',
							fieldname:'',
							address:'',
							unitprice:''
					},
					form:{
						fieldid:'',
						userid:'',
						startDate:'',
						startTime:'',
						endDate:'',
						endTime:'',
						startT:'',
						endT:'',
						unitprice:'',
						phone:''
					},
					sites:[
						{
							fieldid:'',
							fieldimg:'',
							fieldtype:'',
							fieldinout:'',
							fieldstate:'',
							fieldlong:'',
							fieldwidth:'',
							fieldname:'',
							address:'',
							unitprice:''
						}
					],
					currentPage:1,	//
					totalnum:0,		//VueonCreate()axiosajax
					pageNum:1,		//
					pageSizes:[20,40],	//
					nowPageNum:20,	//20
					rules:{
						fieldtype:[
							{required:true,message:'',trigger:'change'}
						],
						phone:[
							{validator:checkLW,trigger:'blur'}
						]
					}	
			}
		},
		methods:{
			handleSizeChange(val){
				this.nowPageNum=`${val}`;
				//console.log(`${val}`);

			},
			 handleCurrentChange(val) {
        		//console.log(`: ${val}`);
        		this.currentPage=parseInt(`${val}`);
        		//console.log(this.currentPage+'-'+this.pageSizes);
     		 },

     		 handleSites(){
     		 	//
     		 	axios({
						url:'http://localhost:8080/GMS/field/getVisibleField.do',
						method:'post',
						data:qs.stringify({'fieldState':''}),
						headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
						},
					}).then(resp => {
						this.sites = resp.data;
						this.totalnum=this.sites.length;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				})
     		 },
     		 showField(index,obj){
     		 	this.dialogFormVisible=true;
     		 	this.tableSite=obj;
     		 },
     		 showOrder(index,obj){
     		 	var data = obj.fieldid;
     		 	this.form.fieldid=obj.fieldid;
     		 	this.form.unitprice=obj.unitprice;
     		 	this.orderFormVisible=true;
     		 	axios({
						url:'http://localhost:8080/GMS/field/getOneField.do',
						method:'post',
						data:qs.stringify({'fieldid':data}),
						headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
						},
					}).then(resp => {
						this.fieldname = resp.data.fieldname;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				})
     		 },
     		 submitForm(formName) {
     		 		this.$refs[formName].validate((valid) => {
					if (valid) {
		     		 	if(this.form.startDate<=this.form.endDate){

		     		 	//	":"  int	
		        		var index1 = this.form.startTime.lastIndexOf('\:');
		        		var index2 = this.form.endTime.lastIndexOf('\:');
		        		//
		        		var tempstart = this.form.startTime.substring(index1+1,this.form.startTime.length);
		        		var tempend = this.form.endTime.substring(index2+1,this.form.endTime.length);
		        		//this.form.startT=this.form.startDate+' '+this.form.startTime;
		        		//console.log(this.form.startTime);
		        		//
		        		var startSeconds = parseInt(this.form.startTime,10)*60*60*1000+parseInt(tempstart,10)*60*1000;
		        		var endSeconds = parseInt(this.form.endTime,10)*60*60*1000+parseInt(tempend,10)*60*1000;
		        		//console.log(startSeconds);
						this.orderFormVisible=false;
						this.form.startT = this.form.startDate+startSeconds;
		        		this.form.endT=this.form.endDate+endSeconds;
		        		//console.log(this.form);
		        		//console.log((this.form.endT-this.form.startT)/60/1000+'');

		        		var tempdata ={
							fieldid:'',
							//id
							userid:'',
							phone:'',
							rentstart:'',
							rentend:'',
							returnid:0,
							allTime:'',
							money:''
						};

						//moment(parseInt(respp[i].rentstart)).format('YYYY-MM-DD hh:mm:ss')
						tempdata.fieldid = this.form.fieldid;
						tempdata.userid = this.form.userid;
						tempdata.phone = this.form.phone;
						tempdata.rentstart = moment(this.form.startT).format('YYYY-MM-DD hh:mm:ss');
						tempdata.rentend = moment(this.form.endT).format('YYYY-MM-DD hh:mm:ss');
						tempdata.allTime = (this.form.endT-this.form.startT)/60/1000;
						tempdata.money = this.form.unitprice*tempdata.allTime/30;

						axios({
							url:'http://localhost:8080/GMS/FieldOrder/orderField.do',
							method:'post',
							headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
							},
							data:qs.stringify(tempdata)
						}).then(resp => {
							//console.log(resp.data);
							var tempC = resp.data;
							if (tempC==='Success to Order') {
								this.$message({
									type: 'success',
									message: ''
								});
								this.reload();
							}

						}).catch(err => {
							console.log('')
						})




		        		//console.log(moment(this.form.startT).format('YYYY-MM-DD hh:mm:ss'));
		        		//console.log(moment(this.form.endT).format('YYYY-MM-DD hh:mm:ss'));
		     		 	
		     		 	}else{
		     		 		this.$notify.error({
								title: '',
								message: '!'
		        			});
		     		 	}	
					} else {
						this.$notify.error({
								title: '',
								message: '!!'
		        			});
						return false;
					}
					});
      		},
			cancelForm(formName){
			//
			this.$refs.form.resetFields();
			this.orderFormVisible=false;
			},
			/*hahaha(){
				var time = new Date();
				var misecond = time.getHours()*60*60*1000+time.getMinutes()*60*1000+time.getSeconds()*1000+time.getMilliseconds();
				console.log(''+(time.getTime()-misecond));
				console.log(''+time.getHours()*60*60*1000);
				console.log(''+time.getMinutes()*60*1000);
				console.log(""+time.getSeconds()*1000);
				console.log(""+time.getMilliseconds());
				//-(time.getDtime.getMinutes()*time.getHours()*time.getSeconds()*time.getMilliseconds())+'  '+time.getTime()+'  '+(time.getMinutes()*time.getHours()*time.getSeconds()*time.getMilliseconds())
			}*/

			getCookieUidAndName(){
				let UsrMsg = this.$getCookie('UName');
				var uID = '';
				var uName = '';
				if (UsrMsg!='') {
					uID = UsrMsg[0];
					uName = UsrMsg[1];
				}
				this.form.userid = parseInt(uID);
				this.name = uName;
			}
		},
		created(){
			this.handleSites();
			this.form.startDate = new Date().getTime()-(new Date().getHours()*60*60*1000+new Date().getMinutes()*60*1000+new Date().getSeconds()*1000+new Date().getMilliseconds());
			this.suretime = new Date().getTime()-(new Date().getHours()*60*60*1000+new Date().getMinutes()*60*1000+new Date().getSeconds()*1000+new Date().getMilliseconds());
			this.getCookieUidAndName();

		},
	}
</script>

<style scoped>
  #notification{
    margin-left: 5px;
    font-size: 10px;
    color: #ff0000;
  }
	.el-row{
		margin-top: 10px;
	}
	.el-col {
		padding-top: 10px;
		border-radius: 4px;
	}
	.bg-purple-dark {
		background: #99a9bf;
	}
	.bg-purple {
		background: #d3dce6;
	}
	.bg-purple-light {
		background: #e5e9f2;
	}
	.grid-content {
		border-radius: 4px;
		min-height: 36px;
	}
</style>


