        		//console.log(`: ${val}`);
        		this.currentPage=parseInt(`${val}`);
        		//console.log(this.currentPage+' '+this.pageSizes);
     		 },

     		 handleSites(){
     		 	//
     		 	/*axios.get('/api/Fieldout').then(resp => {
     		 		console.log(resp.data.errorno);
     		 		if(resp.data.errorno==0){
     		 			console.log('success');
     		 			this.sites=resp.data.data.outplaces;
     		 			// console.log(resp.data.data);
     		 			// console.log(this.sites);
     		 			console.log('assetimgstaticimgloaderrequire()'+'TO:'+'/static/img/1.7f31e1a.jpg&&&/static/img/1.7f31e1a.jpg');
     		 			console.log(require('../../../../../assets/2.jpg')+''+require('../../../../../assets/1.jpg'));
     		 			// 
     		 			this.totalnum=this.sites.length;
     		 		}
     		 	})*/
     		 	axios({
						url:'http://localhost:8080/GMS/field/getInOrOutSide.do',
						method:'post',
						data:qs.stringify({'fieldInOut':''}),
						headers:{
							'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
							'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
						},
					}).then(resp => {
						this.sites = resp.data;
						this.totalnum=this.sites.length;
					//console.log(resp.data);
				}).catch(error =>{
					console.log('err');
				})
     		 },
     		 showField(index,obj){
     		 	this.dialogFormVisible=true;
