			this.handleSites();


/* 		 	console.log('1   '+require('../../../../../../static/img/1.jpg'));
			console.log('2   '+require('../../../../../../static/img/2.jpg'));
			console.log('3   '+require('../../../../../../static/img/billiards.jpg'));
			console.log('4 '+require('../../../../../../static/img/iBadminton.jpg'));
			console.log('5   '+require('../../../../../../static/img/iBasketball.jpg'));
			console.log('6 '+require('../../../../../../static/img/iTableTennis.jpg'));
			console.log('7   '+require('../../../../../../static/img/iVolleyball.jpg'));
			console.log('8 '+require('../../../../../../static/img/oBadminton.jpg'));
			console.log('9   '+require('../../../../../../static/img/oBasketball.jpg'));
			console.log('10  '+require('../../../../../../static/img/oFootball.jpg'));
			console.log('11  '+require('../../../../../../static/img/oTennis.jpg'));
			console.log('12  '+require('../../../../../../static/img/oVolleyball.jpg'));
			*/

		},
		computed:{

		}
	}
</script>

<style scoped>
	.el-row{
		margin-top: 10px;
	}
	.el-col {
		padding-top: 10px;
		border-radius: 4px;
	}
	.bg-purple-dark {
		background: #99a9bf;
	}
	.bg-purple {
		background: #d3dce6;
	}
	.bg-purple-light {
		background: #e5e9f2;
	}
	.grid-content {
		border-radius: 4px;
		min-height: 36px;
	}
</style>


