<template>
	<div class="addField" style="width: 90%;margin-left: 10%">
		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="" prop="fieldname">
    <el-input v-model="ruleForm.fieldname" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="" prop="fieldinout">
    <el-select v-model="ruleForm.fieldinout" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" status-icon prop="fieldtype">
    <el-select v-model="ruleForm.fieldtype" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select> 
  </el-form-item>
  <el-form-item label="" prop="fieldimg" status-icon>
  	<el-select v-model="ruleForm.fieldimg" placeholder="">
		<el-option label="" value="static/img/iBadminton.jpg"></el-option>
		<el-option label="" value="static/img/oBadminton.jpg"></el-option>
		<el-option label="" value="static/img/1.jpg"></el-option>
		<el-option label="" value="static/img/2.jpg"></el-option>
		<el-option label="" value="static/img/iBasketball.jpg"></el-option>
		<el-option label="" value="static/img/oBasketball.jpg"></el-option>
		<el-option label="" value="static/img/iTableTennis.jpg"></el-option>
		<el-option label="" value="static/img/billiards.jpg"></el-option>
		<el-option label="" value="static/img/oTennis.jpg"></el-option>
		<el-option label="" value="static/img/iVolleyball.jpg"></el-option>
		<el-option label="" value="static/img/oVolleyball.jpg"></el-option>
		<el-option label="" value="static/img/oFootball.jpg"></el-option>
	</el-select>
  </el-form-item>
	<div>
		<el-image :src="ruleForm.fieldimg" style="margin-top:10px;width: 400px;height: 200px" lazy>
	      <div slot="error" class="image-slot">
	        <i class="el-icon-picture-outline"></i>
	      </div>
	    </el-image>
	</div>
  </el-form-item>
  <el-form-item label="" required>
  	<el-col :span="8">
  	<el-form-item prop="fieldlong">
  		<span style="margin-left: 1%;margin-right: 1%">()&nbsp;:&nbsp;</span>
    	<el-input v-model.number="ruleForm.fieldlong" placeholder="" ></el-input>
  	</el-form-item>
  	</el-col>
  	<el-col :span="2">&nbsp;</el-col>
  	<el-col :span="8">	
  	<el-form-item prop="fieldwidth">
    <span style="margin-left: 1%;margin-right: 1%">()&nbsp;:&nbsp;</span>
    <el-input v-model.number="ruleForm.fieldwidth" placeholder="" ></el-input>
	</el-form-item>
	</el-col>
  </el-form-item>
  <el-form-item label="" prop="fieldstate">
    <el-radio-group v-model="ruleForm.fieldstate">
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="" prop="address">
    <el-input type="textarea" v-model="ruleForm.address" style="width: 75%"></el-input>
  </el-form-item>
  <el-form-item label="(30)" prop="unitprice">
  	<el-input type="text" v-model.number="ruleForm.unitprice" style="width:75%"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    <el-button @click="resetForm('ruleForm')"></el-button>
  </el-form-item>
</el-form>
	</div>
</template>
<script>
	import axios from 'axios'
	import qs from 'qs'

  export default {
    data() {
    	var checkLW = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error(''));
          } else {
              callback();
          }
        }, 1000);
      };

      return {
        ruleForm: {
			fieldimg:'',	
			fieldtype:'',
			fieldinout:'',
			fieldstate:'',
			fieldlong:'',
			fieldwidth:'',
			fieldname:'',
			address:'',
			unitprice:''

        },
        rules: {
          fieldname: [
            { required: true, message: '', trigger: 'blur' },
            { min: 0, max: 20, message: ' 0  20 ', trigger: 'blur' }
          ],
          fieldinout: [
            { required: true, message: '', trigger: 'change' }
          ],
          fieldtype:[
          	{required:true,message:'',trigger:'change'}
          ],
		  fieldlong:[
		  	{validator:checkLW,trigger:'blur'}
		  ],
		  fieldwidth:[
		  	{validator:checkLW,trigger:'blur'}
		  ],
          fieldstate: [
            { required: true, message: '', trigger: 'change' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' }
          ],
          fieldimg:[
          	{ required: true, message: '', trigger: 'change' }
          ],
          unitprice:[
          	{validator:checkLW,trigger:'blur'}
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
        		const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
          if (valid) {
             //console.log(this.ruleForm);
				var control = false;
				axios({
					url:'http://localhost:8080/GMS/field/addField.do',
					method:'post',
					headers:{
		  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
						'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
  					},
					data:qs.stringify(this.ruleForm)
				}).then(resp => {
					//console.log(this.ruleForm);
					var tempC = resp.data;
					//console.log(tempC);
					if (tempC==='Success to AddField') {
						control=true;
					}
					if (control) {
						setTimeout(() => {
							loading.close();
							this.$notify({
					          title: '',
					          message: '',
					          position:'bottom-right',
					          type: 'success'
				        	});
							return true;
						},1000);
						//
						this.ruleForm={
								fieldimg:'',	
								fieldtype:'',
								fieldinout:'',
								fieldstate:'',
								fieldlong:'',
								fieldwidth:'',
								fieldname:'',
								address:'',
								unitprice:''
        					}
        					//
        					this.$refs[formName].resetFields();
						control=false;
					}else{
						console.log('err')
					}
				})
				
				
          } else {
            console.log('error submit!!');
            
            setTimeout(() => {
            	loading.close();
            	this.$notify({
		          title: '',
		          message: '',
		          position:'bottom-right',
		          type: 'error'
		        });
		        return false;
            },300);
            
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style>
	
</style>


