//
Vue.prototype.$showL=function startLoading(){
  const loading = this.$loading({   //loading
    lock:true,                      //
    text:'...',            //
    background:'rgba(0,0,0,0.5)',   //
    spinner:'el-icon-loading',      //loading
    target:'.ctrlLoading',          //
    body:true,
    customClass:'mask'              //
  })
  setTimeout(function(){
    loading.close();
  },5000)
  return loading;
}



//cookie
   Vue.prototype.$setCookie=function setCookie(name, value, day) {
if (day !== 0) {
		//0expirescookie
var curDate = new Date();
			//curDate
var curTamp = curDate.getTime();
			//curTamp
            //getTime()  1970  1  1 
   var curWeeHours = new Date(curDate.toLocaleDateString()).getTime() - 1;
			//curWeeHours
			//toLocaleDateString()  Date 
var passedTamp = curTamp - curWeeHours;
			//passedTamp
var leftTamp = day * 24 * 60 * 60 * 1000 - passedTamp;
            //console.log(leftTamp);
			//leftTamp
var leftTime = new Date();
leftTime.setTime(leftTamp + curTamp);
		//escape() 
		//toUTCString()  (UTC)  Date
document.cookie = name + "=" + escape(value) + ";expires=" + leftTime.toUTCString();
} else {
document.cookie = name + "=" + escape(value);
}
}


Vue.prototype.$getCookie = function getCookie(name) {
var arr;
	//^n n 
	//n$  n 
	//n?  n 
	//n+  n 
	//n*  n 
var reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
	//unescape()  escape() 
	//match()
if (arr = document.cookie.match(reg)){
	 var cookieString=unescape(arr[2]);
		if(cookieString.length!=0){
		 var cookies=cookieString.split(";");
		 return cookies;
		}
    }
else
return null;
		document.cookie = name + "=" + escape(value);
}




