<template>
  <div class="delReg">
    <el-input placeholder=""  v-model="search" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tables"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="regulationid"
      label="id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regname"
      label=""
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regname)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="regcontent"
      label=""
      width="300%">
      <template slot-scope="scope">
        <span v-html="format(scope.row.regcontent)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="viodispose"
      label=""
      width="300%">
    </el-table-column>
    <el-table-column
      fixed="right"
      label=""
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope/*scope.$index*/, tableData,tables)"
          type="danger"
          size="mini">
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>

  <div style="margin-top:120px;color:#F00;font-size:30px;margin-bottom:60px"></div>

<el-input placeholder=""  v-model="searcho" class="search-input" style="width: 70%;margin-left: 15%;margin-bottom: 1%">
    </el-input>
    <el-table
    :data="tableso"
    border
    style="width: 90%;margin-left: 5%"
    stripe>
    <el-table-column
      fixed
      prop="regulationid"
      label="id"
      width="150%">
    </el-table-column>
    <el-table-column
      prop="regname"
      label=""
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.regname)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="regcontent"
      label=""
      width="300%">
      <template slot-scope="scope">
        <span v-html="formato(scope.row.regcontent)"></span>
      </template>
    </el-table-column>
    <el-table-column
      prop="viodispose"
      label=""
      width="300%">
    </el-table-column>
    <el-table-column
      fixed="right"
      label=""
      style="text-align: center"
      width="200%">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="recycleRow(scope/*scope.$index*/)"
          type="danger"
          size="mini">
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>
<script>
import axios from 'axios'
import qs from 'qs'
export default {
//reload
inject:['reload'],
  data () {
    return {
      search: '',
      searcho:'',
       tableData: [
        {
			regulationid:'',
			regname:'',
			regcontent:'',
			viodispose:''
          }
      ],
      tableDatao: [
        {
            regulationid:'',
			regname:'',
			regcontent:'',
			viodispose:''
          }
      ]
    }
  },

  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }else{
        return this.tableData;
      }
    },
	tableso() {
      const searcho = this.searcho
      if (searcho) {
        return this.tableDatao.filter(dataNews => {
          return Object.keys(dataNews).some(key => {
            return String(dataNews[key]).toLowerCase().indexOf(searcho) > -1
          })
        })
      }else{
        return this.tableDatao;
      }
    }
  },

  methods: {
  	allReg(){
  		axios({
  			url:'http://localhost:8080/GMS/Regulation/getAllReg.do',
  			method:'post',
  			headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
  			},
  		}).then(resp => {
  			this.tableData = resp.data;

  		}).catch(error =>{
  			console.log('err');
  		});

      //
      axios({
        url:'http://localhost:8080/GMS/Regulation/getAllRegD.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
        'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
        },
      }).then(resp => {
        this.tableDatao = resp.data;

      }).catch(error =>{
        console.log('err');
      })
  	},
     deleteRow(scope, rows,searchTable) {
		var control = false;
     	this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {

        	axios({
     		url:'http://localhost:8080/GMS/Regulation/delFieldReg.do',
     		method:'post',
     		headers:{
  				'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
				'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
  			},
  			data:qs.stringify({'regulationid':scope.row.regulationid}),
     	}).then(resp => {
     			var tempBool = resp.data;
     		if (tempBool = "Success to DelReg") {
     			control = true;
     		}
     		if (control) {
     			const loading = this.$loading({
		          lock: true,
		          text: 'Loading',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
				setTimeout(() => {
					loading.close();
					this.$notify({
				      title: '',
				      message: '',
				      position:'bottom-right',
				      type: 'success'
					});
					//
					this.reload();
					control = false;
				},1000);
     		}else{
     			console.log('err');
     		}
     	}).catch(err => {
     		console.log('err');
     	});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
      recycleRow(scope){
      	//
      //
      var control = false;
      axios({
        url:'http://localhost:8080/GMS/Regulation/recycleDelReg.do',
        method:'post',
        headers:{
          'Content-Tpye':'application/json;charset=UTF-8;charset=UTF-8',//resputf-8
          'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8',//reqpayloadutf-8
        },
        data:qs.stringify({'regulationid':scope.row.regulationid}),
      }).then(resp => {
          var tempBool = resp.data;
        if (tempBool = "Success to RecycleReg") {
          control = true;
        }
        if (control) {
          const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
        setTimeout(() => {
          loading.close();
          this.$notify({
              title: '',
              message: '',
              position:'bottom-right',
              type: 'success'
          });
          //
          this.reload();
          control = false;
        },1000);
        }else{
          console.log('err');
        }
      }).catch(err => {
        console.log('err');
      });
      },
    format (val) {
      if (val.indexOf(this.search) !== -1 && this.search !== '') {
        return val.replace(this.search, '<font color="red">' + this.search + '</font>')
      } else {
        return val;
      }
    },
  formato (val) {
      //val.indexOf(this.search) !== -1 &&
      if (val.indexOf(this.searcho)!==-1&& this.searcho !== '') {
        return val.replace(this.searcho, '<font color="red">' + this.searcho + '</font>')
      } else {
        return val;
      }
    },
  },
  created(){
  	this.allReg();
  }

}
</script>

<style scoped>

</style>


